const ADMIN_URL = "http://localhost:8000/admin"

async function httpGetUsers() {
  const response = await fetch(`${ADMIN_URL}/users`);
  return await response.json();
}
async function httpDeleteUser(id) {
  try {
    return await fetch(`${ADMIN_URL}/users/${id}`, {
      method: "DELETE",
    });
  } catch (err) {
    return {
      ok: false,
    };
  }
}

export { httpGetUsers };
